/*　画像表示
----------------------------------------------- */
.is-square {
  position: relative;
  opacity: 0;
  overflow: hidden;
}
.is-square::before {
  content: "";
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  transition: 1s ease-in-out;
  transition-delay: 0.3s;
  background-color: rgba(0, 0, 0, 0.6);
}
.is-square::after {
  content: "";
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  background: #ffffff;
  transition: 0.5s ease-in-out;
  transition-delay: 0.3s;
}
.is-square.show {
  opacity: 1;
}
.is-square.show::before {
  background-color: rgba(0, 0, 0, 0);
}
.is-square.show::after {
  width: 0;
}